.nav-bar {
  display: flex;
  justify-content: space-between;
  background-color: black;
  color: white;
}
.nav-link {
  width: 150px;
  height: 50px;
  text-align: center;
  padding-top: 25px;
  color: white;
}
a {
  text-decoration: none;
  color: white;
  align-items: center;
}

.nav-link:hover {
  background-color: rgb(44, 43, 43);
  color: white;
}
